/*
0 - 600px   Phone
600 - 900px     Tablet portrait
900 - 1200px    Tablet landscape
1200 - 1800px   Normal styles applied
1800px+    Big Desktop
*/
.header {
  height: 8vh;
}
.header--fixed {
  position: fixed;
  width: 100%;
  background-color: white;
}
.header__logo {
  z-index: 2001;
  transition: position 0.5s ease;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.25rem;
}

.stuffed {
  height: 10.1vh;
}

.body {
  height: 180rem;
  background-color: bisque;
}

.navigation__checkbox {
  position: absolute;
  appearance: none;
  z-index: -1;
}
.navigation__button {
  display: none;
  background-color: transparent;
  height: 1.875rem;
  width: 1.875rem;
  z-index: 2000;
  cursor: pointer;
  transition: all 0.8s;
}
@media (max-width: 52.25em) {
  .navigation__button {
    position: fixed;
    display: block;
    top: 2.1875rem;
    right: 1.5625rem;
  }
}
@media (max-width: 52.25em) {
  .navigation__icon {
    position: relative;
    margin-top: 0.875rem;
  }
  .navigation__icon, .navigation__icon::before, .navigation__icon::after {
    width: 1.5625rem;
    height: 2px;
    background-color: var(--body);
    display: inline-block;
    transition: all 0.2s;
  }
  .navigation__icon::before, .navigation__icon::after {
    content: "";
    position: absolute;
    left: 0;
  }
  .navigation__icon::before {
    top: -0.625rem;
  }
  .navigation__icon::after {
    top: 0.625rem;
  }
}
.navigation__button:hover .navigation__icon::before {
  top: -0.6875rem;
}
.navigation__button:hover .navigation__icon::after {
  top: 0.6875rem;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}
.navigation__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 52.25em) {
  .navigation__nav {
    display: none;
  }
}
.navigation__list {
  font-size: 1.25rem;
  display: flex;
  gap: 1.25rem;
  list-style: none;
  text-align: center;
  width: 100%;
}
@media (max-width: 52.25em) {
  .navigation__list {
    display: block;
  }
}
@media (max-width: 52.25em) {
  .navigation__item {
    display: none;
  }
}
.navigation__link {
  color: var(--body);
  text-decoration: none;
  text-transform: uppercase;
}
.navigation__link--home.active {
  color: var(--primary);
}
.navigation__socials {
  display: none;
}
.navigation__socials__item {
  height: 1.5rem;
  width: 1.5rem;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
}
.navigation__socials__item::before {
  content: "";
  height: 4rem;
  width: 4rem;
  background-color: var(--grey1);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
}
.navigation__socials__item--facebook {
  background-image: url(../../../images/FB.png);
}
.navigation__socials__item--twitter {
  background-image: url(../../../images/TW.png);
}
.navigation__socials__item--instagram {
  background-image: url(../../../images/INST.png);
}
.navigation__socials__item--twitter2 {
  background-image: url(../../../images/TW.png);
}
.navigation__checkbox:checked ~ .navigation__background {
  height: 100%;
  transition: height 0.5s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 40%;
  transform: translate(0, -50%);
  left: 0.125rem;
  background-color: transparent;
  z-index: 1000;
  animation: moveNavInLeft 0.8s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__nav .navigation__list {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-evenly;
}
.navigation__checkbox:checked ~ .navigation__nav .navigation__item {
  display: inline-block;
  margin: 1.25rem;
  animation: fadeInUp 0.5s ease-in-out;
}
.navigation__checkbox:checked ~ .navigation__socials {
  display: block;
  position: fixed;
  bottom: 5rem;
  left: 0.125rem;
  z-index: 1000;
  width: 100%;
  animation: moveInBottom 0.8s ease-in-out 0.5s;
  animation-fill-mode: backwards;
}
.navigation__checkbox:checked ~ .navigation__socials > .navigation__socials__container {
  display: flex;
  justify-content: space-around;
}
.navigation__checkbox:checked ~ .navigation__socials > .navigation__socials__container a {
  border-radius: 50%;
  padding: 2rem;
}
.navigation__background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background-color: var(--white);
  z-index: 100;
  transition: all 0.7s ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--white);
  font-family: var(--font-default);
  font-size: 1.25rem;
  line-height: 2rem;
  color: var(--body);
}
@media (max-width: 52.25em) {
  body {
    font-size: 1.125rem;
  }
}

h1 {
  font-size: 4.375rem;
  line-height: 5.25rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 52.25em) {
  h1 {
    font-size: 3.375rem;
    line-height: 4rem;
  }
}
h2 {
  font-size: 3rem;
  line-height: 3.5rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 52.25em) {
  h2 {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}
h3 {
  font-size: 2.5rem;
  line-height: 3rem;
  letter-spacing: 0.36px;
}

h4 {
  font-size: 1.875rem;
  line-height: 2rem;
  font-weight: 400;
}

.displayText {
  font-family: var(--font-display);
  color: var(--primary);
  text-transform: capitalize;
  font-size: 2.25rem;
  line-height: 4rem;
  font-weight: normal;
}

@media (max-width: 52.25em) {
  .displayText {
    font-size: 1.75rem;
    line-height: 3rem;
    font-weight: normal;
  }
}
.subtitle1 {
  font-size: 1.375rem;
}
.subtitle1--bold {
  font-size: 1.375rem;
  font-weight: 700;
}

.subtitle2 {
  font-size: 1.5rem;
}
.subtitle2--bold {
  font-size: 1.5rem;
  font-weight: 700;
}

.label {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1rem;
  color: var(--white);
  background-color: var(--body);
  border-radius: 0.5rem;
  padding: 0.625rem 1.5625rem;
  display: flex;
  justify-content: center;
}
.label--bold {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--white);
  background-color: var(--body);
  border-radius: 0.5rem;
  padding: 0.625rem 1.5625rem;
  display: flex;
  justify-content: center;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --white: #fff;
  --grey1: #f7f7f7;
  --grey2: #f1f1f1;
  --grey3: #d4d4d4;
  --grey4: #b8b8b8;
  --grey5: #525c60;
  --black: #000000;
  --primary: #7eb693;
  --secondary: #ffa858;
  --body: #274c5b;
}

:root {
  --font-default: "Roboto", sans-serif;
  --font-display: "Yellowtail", cursive;
}

@keyframes moveNavInLeft {
  0% {
    opacity: 0;
    transform: translate(-10rem, -50%);
  }
  80% {
    transform: translate(1rem, -50%);
  }
  100% {
    opacity: 1;
    transform: translate(0, -50%);
  }
}
@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.hero {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-image: url(/images/bg_images/Hero.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left;
  height: 80vh;
  transition: all 0.4s;
}
@media (max-width: 52.25em) {
  .hero {
    height: 70vh;
    background-position: 30% center;
  }
}
.hero__text {
  padding-left: 2.5rem;
  grid-column: 1/2;
  align-self: center;
}
@media (max-width: 52.25em) {
  .hero__text {
    grid-column: 1/-1;
    padding-right: 7.5rem;
  }
}
.hero__title {
  margin-bottom: 2.1875rem;
}

.offerCard {
  display: grid;
  padding: 3.125rem 1.25rem;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.25rem;
}
@media (max-width: 52.25em) {
  .offerCard {
    display: block;
  }
}
.offerCard__card {
  position: relative;
  border-radius: 2rem;
  height: 24rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: all 0.3s;
  z-index: -1;
}
.offerCard__card:nth-of-type(1) .offerCard__card__displayText,
.offerCard__card:nth-of-type(1) .offerCard__card__title {
  color: var(--white);
}
@media (max-width: 52.25em) {
  .offerCard__card {
    background-position: 30% center;
  }
  .offerCard__card:not(:last-child) {
    margin-bottom: 1.25rem;
  }
}
.offerCard__card__container {
  max-width: 18.75rem;
  position: absolute;
  left: 1.875rem;
  top: 50%;
  transform: translate(0, -50%);
}
.offerCard__card--1 {
  background-image: url(/images/bg_images/OfferCard1.png);
}
.offerCard__card--2 {
  background-image: url(/images/bg_images/OfferCard2.png);
}

.aboutUs {
  display: flex;
  justify-content: center;
  background-color: var(--grey1);
}
@media (max-width: 52.25em) {
  .aboutUs {
    flex-direction: column;
    align-items: center;
    padding: 1.25rem;
  }
}
.aboutUs__sideImage {
  align-self: center;
  margin: 7.5rem 0.625rem 7.5rem 2.5rem;
}
@media (max-width: 52.25em) {
  .aboutUs__sideImage {
    margin: 2.5rem 0 0rem 0;
  }
}
.aboutUs__sideImage img {
  height: 41.5rem;
  max-width: 100%;
  min-width: 18.75rem;
  object-fit: contain;
}
@media (max-width: 52.25em) {
  .aboutUs__sideImage img {
    height: auto;
    width: 80vw;
  }
}
.aboutUs__informationContainer {
  flex-shrink: 1;
  margin: 5rem 2.5rem 5rem 0.625rem;
  max-width: 34.5rem;
}
@media (max-width: 52.25em) {
  .aboutUs__informationContainer {
    margin: 2.5rem 0;
  }
}
.aboutUs__title {
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.aboutUs__information {
  font-size: 1.125rem;
  margin-bottom: 3.5rem;
}
@media (max-width: 52.25em) {
  .aboutUs__information {
    margin-bottom: 2.5rem;
  }
}
.aboutUs__characteristics {
  margin-bottom: 3.5rem;
  list-style: none;
}
@media (max-width: 52.25em) {
  .aboutUs__characteristics {
    margin-bottom: 2.5rem;
  }
}
.aboutUs__characteristics--item {
  margin-bottom: 1.5rem;
}
.aboutUs__characteristics--itemText {
  font-size: 1.125rem;
}

.products {
  padding: 5rem 1.25rem;
  text-align: center;
}
.products__title {
  margin-bottom: 2.5rem;
}
.products__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-content: center;
  gap: 1.25rem;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 82.72em) {
  .products__container {
    grid-template-columns: repeat(2, 1fr);
    max-width: 56.25rem;
    gap: 0.9375rem;
  }
}
@media (max-width: 52.25em) {
  .products__container {
    display: block;
  }
}

.testimonials {
  display: flex;
  flex: 1/3fr 1fr 1/3fr;
  justify-content: space-between;
  align-items: center;
  background-color: #fcfcfc;
  position: relative;
}
.testimonials__leftImage, .testimonials__rightImage {
  background-size: cover;
}
@media (max-width: 52.25em) {
  .testimonials__leftImage, .testimonials__rightImage {
    display: none;
  }
}
.testimonials__container {
  flex: 1;
  max-width: 900px;
  padding: 10rem 1.25rem;
  margin: 0 auto;
  text-align: center;
}
@media (max-width: 82.72em) {
  .testimonials__container {
    padding: 5rem 1.25rem;
  }
}
.testimonials__title {
  margin-bottom: 3.375rem;
}
.testimonials__profileImage {
  border-radius: 100%;
}
.testimonials__stars {
  margin-bottom: 1.25rem;
}
.testimonials__stars--star {
  width: 1.5rem;
  height: 1.5rem;
}
.testimonials__text {
  margin-bottom: 1.25rem;
}
.testimonials__name {
  margin-bottom: 0.625rem;
}
.testimonials__label {
  color: var(--grey5);
  font-size: 0.875rem;
  line-height: 1rem;
}

.offerProducts {
  padding: 4rem 1.25rem;
  position: relative;
  background-color: var(--body);
  z-index: -10;
}
.offerProducts__title {
  color: var(--white);
}
@media (max-width: 52.25em) {
  .offerProducts__title {
    max-width: 21.875rem;
    margin-bottom: 1.25rem;
  }
}
.offerProducts__topSectionContainer {
  display: grid;
  gap: 1.25rem;
  grid-template-columns: 1fr auto;
  margin-bottom: 2.1875rem;
}
@media (max-width: 52.25em) {
  .offerProducts__topSectionContainer {
    display: block;
  }
}
.offerProducts__button {
  margin-top: -1.25rem;
  align-self: flex-start;
}
@media (max-width: 52.25em) {
  .offerProducts__button {
    margin-top: 0rem;
  }
}

.ecoFriendly__container {
  display: grid;
  grid-template-columns: 9fr 4fr 8fr;
}
@media (max-width: 52.25em) {
  .ecoFriendly__container {
    display: grid;
    grid-template-rows: 2fr 1fr 7fr;
    grid-template-columns: 1fr;
  }
}
.ecoFriendly__image {
  width: 100%;
  background-image: url(/images/bg_images/EcoFriendly.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  grid-column: 1/3;
  grid-row: 1/2;
}
@media (max-width: 52.25em) {
  .ecoFriendly__image {
    grid-row: 1/3;
    grid-column: 1;
  }
}
.ecoFriendly__overlay-card {
  min-height: 35.625rem;
  max-width: 45rem;
  border-radius: 2rem;
  background-color: var(--white);
  box-shadow: 1rem 1.5rem 3.25rem -0.5rem rgba(0, 0, 0, 0.2);
  padding: 1.5625rem 2.25rem 3.125rem 2.25rem;
  margin: 5vh 5vw;
  grid-column: 2/4;
  grid-row: 1/2;
}
@media (max-width: 52.25em) {
  .ecoFriendly__overlay-card {
    grid-row: 2/4;
    grid-column: 1;
    margin-bottom: 0;
  }
}
.ecoFriendly__title {
  margin-bottom: 2.5rem;
}
.ecoFriendly__list {
  list-style: none;
}
.ecoFriendly__list--item:not(:nth-last-of-type(1)) {
  margin-bottom: 0.625rem;
}

.button {
  color: var(--body);
}
.button, .button:link, .button:visited {
  text-transform: capitalize;
  text-decoration: none;
  gap: 0.9375rem;
  padding: 1.5rem;
  border-radius: 1rem;
  border: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
}
@media (max-width: 52.25em) {
  .button, .button:link, .button:visited {
    padding: 1rem 1.5rem;
  }
}
.button__arrow {
  width: 100%; /* Make it responsive to parent element */
  max-width: 2.3125rem;
  max-height: 0.9375rem;
}
.button__arrow--blue {
  fill: transparent;
  stroke: var(--body);
}
.button__arrow--white {
  fill: transparent;
  stroke: var(--white);
}
.button--orange {
  background-color: var(--secondary);
}
.button--blue {
  color: var(--white);
  background-color: var(--body);
}
.button--bordered {
  background-color: transparent;
}

.card__container {
  padding: 1.875rem;
  max-width: 25rem;
  border-radius: 2rem;
  background-color: var(--grey1);
  position: relative;
  z-index: -10;
}
@media (max-width: 52.25em) {
  .card__container {
    margin: 0 auto;
  }
  .card__container:not(:nth-last-child(1)) {
    margin-bottom: 0.9375rem;
  }
}
.card__topSection {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-between;
  border-bottom: 1px solid var(--grey4);
}
.card__image {
  max-width: 23rem;
  width: 100%;
  height: auto;
  margin: 0 auto;
}
.card__subtitle1 {
  padding: 0.625rem 0;
}
.card__bottomSection {
  padding-top: 0.625rem;
  display: flex;
  flex: auto auto 1fr;
  gap: 0.625rem;
}
@media (max-width: 52.25em) {
  .card__bottomSection {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.card__crossedOutPrice {
  font-size: 1.125rem;
  color: var(--grey4);
  position: relative;
  z-index: -1;
}
.card__crossedOutPrice::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  height: 1.2px;
  width: 100%;
  background-color: var(--grey4);
}
@media (max-width: 52.25em) {
  .card__crossedOutPrice {
    grid-column: 1/2;
    margin-right: auto;
    margin-left: 0;
  }
}
.card__price {
  font-size: 1.25rem;
  font-weight: 700;
}
@media (max-width: 52.25em) {
  .card__price {
    grid-column: 2/3;
    margin-left: auto;
    margin-rigth: 0;
  }
}
.card__stars {
  margin-left: auto;
}
@media (max-width: 52.25em) {
  .card__stars {
    grid-column: 1/2;
    grid-row: 2/3;
    margin-right: auto;
    margin-left: 0;
  }
}
.card__stars--star {
  width: 1rem;
  height: 1rem;
  max-width: 100%;
  max-height: 100%;
}
@media (max-width: 82.72em) {
  .card__stars--star {
    margin: 0 auto;
    width: 0.875rem;
    height: 0.875rem;
  }
}